export default {
  title: '¿En qué consiste este regalo?',
  description: [
    'El 2010 egresados de Ingeniería UC fundaron la tradición de que cada generación de titulados haga un regalo a los estudiantes del programa Talento + Inclusión de la Escuela. Este año generamos una nueva propuesta: darle la oportunidad a ex-alumnos y ex-alumnas para participar dando consejos o compartiendo experiencias con estudiantes apoyando la implementación de un Piloto del Programa de Mentoría Exalumnos que implementará la Escuela en 2019 y que se quiere escalar a todos los ex-alumnos y alumnos.',
  ],
};



